





























































import { Component, Vue, Watch } from "vue-property-decorator";
import QMenu from "../../components/qTouch/menu/QMenu.vue";
import { QMenuItem } from "@/components/qTouch/menu/interface";
import api from "@/microApps/navBar/api";
import cookieUtils from "@/utils/cookie";
import utils from "@/utils/utils";
import Url from "@/microApps/root/api";

@Component({
  components: { QMenu }
})
export default class NavBar extends Vue {
  menuData: Array<QMenuItem> = [];
  hashMap: any = {};

  realName: string = localStorage.getItem("realName") || "";
  defaultActive: string = location.href.split(location.origin + qTouch.basePath)[1] || "";
  fullscreen: boolean = false;
  locationInfo: string[] = [];

  currentTeam: string = "";
  teamList: Array<any> = [];

  // 同步菜单高亮和当前位置显示
  @Watch("$route.path")
  handlePathChange(path: string) {
    this.defaultActive = path.split(qTouch.basePath)[1] || "";
    let currentPage = (this.$refs.elMenu as any).items[this.defaultActive];
    currentPage && this.handleSelectMenu(currentPage.index, currentPage.indexPath);
  }

  created() {
    let menus = localStorage.getItem("menus");
    if (menus) {
      this.menuData = JSON.parse(menus);
      utils.convertMenus(this.menuData, this.hashMap);
    }
    // 添加事件监听-全屏
    qTouch.on("switchFullscreen", this.switchFullscreen);
    this.fullscreen = !!localStorage.getItem("isFullScreen");
    if (this.fullscreen) {
      document.body.classList.add("full-screen");
    } else {
      document.body.classList.remove("full-screen");
    }
    // 修改用户名
    qTouch.on("changeName", this.handleChangeName);
    window.addEventListener("single-spa:routing-event", this.handleRouteChange);
    this.getTeamList();
    this.getUserInfo();
  }

  mounted() {
    let currentPage = (this.$refs.elMenu as any).items[this.defaultActive];
    currentPage && this.handleSelectMenu(currentPage.index, currentPage.indexPath);
  }

  beforeDestroy() {
    qTouch.off("switchFullscreen", this.switchFullscreen);
    qTouch.off("changeName", this.handleChangeName);
    window.removeEventListener("single-spa:routing-event", this.handleRouteChange);
  }

  switchFullscreen(flag: boolean) {
    flag ? localStorage.setItem("isFullScreen", "1") : localStorage.removeItem("isFullScreen");
    location.reload();
  }

  handleChangeName(name: string) {
    this.realName = name;
  }

  // 临时菜单组装方法
  static genMenus(menus: Array<QMenuItem>, index: number, parent: string) {
    index++;
    menus &&
      menus.forEach(item => {
        if (index === 1) {
          item.path = parent + item.path.substr(1);
        }
        if (index === 2) {
          item.path = parent + item.path;
        }
        NavBar.genMenus(item.children, index, item.path);
      });
  }

  handleSelect(item: QMenuItem) {
    if (item.target === "_self") {
      if (item.pathType === 1) {
        qTouch.navigateToUrl(item.path);
      } else if (item.pathType === 3) {
        this.getDetailAddress(item.path, item.target);
      } else {
        qTouch.navigateToUrl("/qTouch/openPath?path=" + item.path);
      }
    } else {
      if (item.pathType === 3) {
        this.getDetailAddress(item.path, item.target);
      } else {
        window.open(item.path, item.target);
      }
    }
  }

  async getDetailAddress(path: string, target: string) {
    let {
      data: { data, status, message }
    } = await this.$http.get(path);
    if (status) {
      console.log(data);
      if (target === "_self") {
        qTouch.navigateToUrl("/qTouch/openPath?path=" + data);
      } else {
        window.open(data, target);
      }
    } else {
      this.$message.error(message);
    }
  }

  // 更新面包屑导航条
  handleSelectMenu(index: string, indexPath: string[]) {
    let current = this.menuData;
    this.locationInfo = [];
    indexPath.forEach(path => {
      let item = current.find(d => d.path === path);
      if (item) {
        this.locationInfo.push(item.name);
        current = item.children;
      }
    });
  }

  // 跳转root页面
  handleToRoot() {
    qTouch.navigateToUrl(qTouch.appPathMap.root);
  }

  handleFoldMenu() {
    qTouch.emit("switchFullscreen", true);
  }

  handleUnFoldMenu() {
    qTouch.emit("switchFullscreen", false);
  }

  // 获取用户可选团队列表
  getTeamList() {
    this.$http.get(api.getTeamList).then((res: any) => {
      if (res.data.status) {
        this.teamList = res.data.data;
        for (let i in this.teamList) {
          // 获取当前所在团队
          if (this.teamList[i].currentFlag) {
            this.currentTeam = this.teamList[i].tenantId;
            break;
          }
        }
      } else {
        this.$message.error(res.data.message);
      }
    });
  }

  // 获取用户信息
  getUserInfo() {
    this.$http.get(Url.getAccountInfo).then((res: any) => {
      if (res.data.status) {
        localStorage.setItem("realName", res.data.data.realName);
      }
    });
  }

  // 切换团队
  changeTeam(val: string) {
    this.$confirm("确定切换？", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    })
      .then(() => {
        this.$http
          .get(api.changeTeam, {
            params: {
              tenantId: this.currentTeam
            }
          })
          .then((res: any) => {
            if (res.data.status) {
              let data = res.data.data;
              this.$message.success("切换成功");
              localStorage.clear();
              cookieUtils.set("accessToken", data.accessToken);
              localStorage.setItem("menus", JSON.stringify(data.menus));
              localStorage.setItem("realName", data.realName);
              localStorage.setItem("userName", data.username);
              // 存储基本数据类型权限Map
              let baseTypeCodeMap: { [key: string]: boolean } = {};
              data.baseTypes.forEach((item: any) => {
                baseTypeCodeMap[item.code] = true;
              });
              localStorage.setItem("baseTypeCodeMap", JSON.stringify(baseTypeCodeMap));
              qTouch.navigateToUrl(qTouch.appPathMap.root);
              window.location.reload();
            } else {
              this.$message.error(res.data.message);
            }
          });
      })
      .catch(() => {
        // 获取当前所在团队
        for (let i in this.teamList) {
          // 获取当前所在团队
          if (this.teamList[i].currentFlag) {
            this.currentTeam = this.teamList[i].tenantId;
            break;
          }
        }
      });
  }

  // 退出登录
  async logout() {
    this.$confirm("确定退出？", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    })
      .then(() => {
        this.$http.post(api.logout).then((res: any) => {
          if (res.data.status) {
            localStorage.clear();
            cookieUtils.remove("accessToken");
            qTouch.navigateToUrl(qTouch.appPathMap.login);
          } else {
            this.$message.error(res.data.message);
          }
        });
      })
      .catch(() => {});
  }

  // 跳转个人中心
  goPersonInfo(path: string) {
    if (path === "/personInfo") {
      this.locationInfo = ["个人中心"];
    } else {
      this.locationInfo = ["修改密码"];
    }
    qTouch.navigateToUrl(qTouch.appPathMap.root + path);
  }

  // 首次访问
  handleToHistoryPath() {
    if (location.pathname === "/") {
      let historyPath = localStorage.getItem("historyPath");
      if (historyPath) {
        qTouch.navigateToUrl(historyPath);
      } else {
        qTouch.navigateToUrl(qTouch.appPathMap.login);
      }
    }
  }

  // 路由变化
  handleRouteChange() {
    // 存储历史访问路径
    if (location.pathname.startsWith(qTouch.basePath)) {
      localStorage.setItem("historyPath", location.pathname + location.hash);
    } else {
      localStorage.setItem("historyPath", "");
    }
    // 403
    let pathname = window.location.pathname;
    if (pathname === "/") {
      // 通过域名或ip访问直接跳登录页
      qTouch.navigateToUrl(qTouch.appPathMap.login);
    } else if (/\/root/.test(pathname) || /\/login/.test(pathname) || /\/error\//.test(pathname)) {
      // 主页，登录页，个人中心不拦截
    } else if (!this.hashMap[pathname]) {
      // qTouch.navigateToUrl(qTouch.appPathMap.errorPage + "/403");
    }
    // 404
    let appName = location.pathname.match(new RegExp(qTouch.basePath + `(/[^/]+)`));
    if (location.pathname !== "/" && (!appName || !Object.values(qTouch.appPathMap).includes(appName[1]))) {
      // todo 404逻辑
      // qTouch.navigateToUrl(qTouch.appPathMap.errorPage + "/404");
    }
  }
}
